$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Block Directory
.wp-block-ilb-icon-list {
	text-align: center;

	* {
		box-sizing: border-box;
	}

	.ilbIconList {
		max-width: 100%;
		display: inline-block;
		// width: 560px;
		// height: 320px;

		// &.theme1, &.theme2{
		// 	display: inline-flex;
		// 	align-items: center;
		// 	justify-content: space-between;

		// 	.contacts{
		// 		max-width: 50%;
		// 	}
		// }
		// &.theme2{
		// 	align-items: flex-start;
		// 	flex-direction: row-reverse;
		// }

		&.left {
			.header {
				justify-items: start;
			}
		}

		&.center {
			text-align: center;

			.header {
				justify-items: center;
			}

			ul.lists {
				display: inline-block;
			}
		}

		&.right {
			.header {
				justify-items: end;
			}

			ul.lists .list {
				flex-direction: row-reverse;
			}
		}

		.header {
			display: grid;
			height: min-content;

			.title,
			.description {
				margin: 0 0 5px 0;
				text-align: initial;
				width: auto;
			}

			.separator {
				text-align: initial;
				display: inline-block;
			}
		}

		ul.lists {
			list-style-type: none;
			padding: 0;
			margin: 0;

			li {
				display: flex;
				align-items: center;
				gap: 15px;
				padding: 0;

				&:not(:last-child) {
					margin-bottom: 8px;
				}

				.icon {
					font-style: normal;
					display: flex;
					align-items: center;
					justify-content: center;
					border-radius: 50%;
				}

				.link {
					display: inline-block;
					text-decoration: none;
				}

				.text {
					margin: 0;
					text-align: left;
				}
			}
		}
	}
}